
.do {
  width: 70%;
  margin: 30px auto;
  height: auto;
}
.do .todo-wrapper {
    min-height: 16px;
    width: 88%;
    margin: 14px auto 10px;
    -webkit-transition: height 1s;
    transition: height 1s;
}
.do .todo-wrapper .todo {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-transition: all 1s;
      transition: all 1s;
      margin-bottom: 8px;
}
.do .todo-wrapper .todo.list-enter-active, .do .todo-wrapper .todo.list-leave-active {
        -webkit-transition: all 1s;
        transition: all 1s;
        position: absolute;
}
.do .todo-wrapper .todo.list-enter, .do .todo-wrapper .todo.list-leave-to {
        opacity: 0;
        -webkit-transform: translateY(30px);
                transform: translateY(30px);
}
.do .todo-wrapper .todo.list-move {
        -webkit-transition: -webkit-transform 1s;
        transition: -webkit-transform 1s;
        transition: transform 1s;
        transition: transform 1s, -webkit-transform 1s;
}
.do .todo-wrapper .text {
      margin-left: 5px;
}



