
#app {
  width: 80%;
  display: block;
  margin: 0 auto;
  text-align: center;
}
#app .input {
    display: block;
    width: 80%;
    margin: 0 auto;
    height: 40px;
    line-height: 40px;
    margin-top: 200px;
    border: 1px solid #DCDCDC;
    border-radius: 11px;
    text-align: center;
    font-size: 20px;
}
#app .do-group {
    -webkit-transition: all 1s;
    transition: all 1s;
    display: inline-block;
    text-align: left;
}
#app .do-group.list-enter-active, #app .do-group.list-leave-active {
      -webkit-transition: all 1s;
      transition: all 1s;
      position: absolute;
}
#app .do-group.list-enter, #app .do-group.list-leave-to {
      opacity: 0;
      -webkit-transform: translateY(30px);
              transform: translateY(30px);
      height: 0;
}
#app .done-group {
    text-align: left;
    -webkit-transition: all 1s;
    transition: all 1s;
    display: inline-block;
}
#app .done-group.list-enter-active, #app .done-group.list-leave-active {
      -webkit-transition: all 1s;
      transition: all 1s;
      position: absolute;
}
#app .done-group.list-enter, #app .done-group.list-leave-to {
      opacity: 0;
      -webkit-transform: translateY(30px);
              transform: translateY(30px);
      height: 0;
}



